import { MongoClient } from 'mongodb'

const uri = process.env.MONGODB_URI || 'mongodb://localhost:27017/patisserie'
const options = {}

let client: MongoClient | null = null
let clientPromise: Promise<MongoClient> | null = null

// Créer la connexion seulement si MongoDB est configuré
if (process.env.MONGODB_URI && !process.env.MONGODB_URI.includes('localhost')) {
  if (process.env.NODE_ENV === 'development') {
    // En développement, utilise un cache global pour éviter de multiples connexions
    const globalWithMongo = global as typeof globalThis & {
      _mongoClientPromise?: Promise<MongoClient>
    }

    if (!globalWithMongo._mongoClientPromise) {
      client = new MongoClient(uri, options)
      globalWithMongo._mongoClientPromise = client.connect()
    }
    clientPromise = globalWithMongo._mongoClientPromise
  } else {
    // En production, crée une nouvelle connexion
    client = new MongoClient(uri, options)
    clientPromise = client.connect()
  }
} else {
  console.log('Mode démonstration : MongoDB non configuré')
}

export default clientPromise
